<template>
  <div>
    <!-- 面包屑导航 -->
    <div class="breadcrumb">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item class="breadcrumb_item" :to="{ path: '/home' }">首页</el-breadcrumb-item>
        <el-breadcrumb-item :to="{ path: '/standardVideo' }">标准生活动漫视频</el-breadcrumb-item>
        <el-breadcrumb-item>{{ $route.params.type === 'edit' ? '修改视频' : '新增视频' }}</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <el-card>
      <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="140px" class="demo-ruleForm">
        <el-form-item label="标题" prop="title">
          <el-input v-model="ruleForm.title" placeholder="请输入标题"></el-input>
        </el-form-item>
        <el-form-item label="缩略图" prop="thumbnail" class="thumbnail">
          <el-upload
            class="avatar-uploader-logo"
            :action="uploadAction()"
            :show-file-list="false"
            :on-success="(res, file, fileList) => handleAvatarSuccess(res, file, fileList, 0)"
            :before-upload="beforeAvatarUpload"
          >
            <img v-if="ruleForm.thumbnail" :src="videoBaseUrl + ruleForm.thumbnail" class="avatar" />
            <i v-else class="el-icon-plus avatar-uploader-icon"><i></i></i>
          </el-upload>
          <div class="font_size">
            <span>支持jpg／png格式</span>
            <!-- <span class="tips">小程序首页，头部切换门店及搜索栏背景图</span> -->
          </div>
        </el-form-item>
        <el-form-item label="来源选择" prop="type">
          <el-radio-group v-model="ruleForm.type">
            <el-radio label="1">外部网站</el-radio>
            <el-radio label="2">本地上传</el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item v-if="ruleForm.type == 1" label="外部网站访问地址" prop="webUrl">
          <el-input v-model="ruleForm.webUrl" placeholder="请输入外部网站访问地址"></el-input>
        </el-form-item>

        <!-- 视频上传 -->
        <el-form-item v-if="ruleForm.type == 2" label="本地文件" class="local">
          <el-upload accept=".mp4" class="upload-demo" :action="uploadAction()" :on-success="uploadSuccess" :beforeUpload="beforeExcelUpload">
            <i class="el-icon-upload"></i>
            <div class="el-upload__text"><i class="el-icon-upload2"></i> 上传</div>
          </el-upload>
          <div>
            <span>请上传本地文件</span>
          </div>
          <div v-if="fileName" class="tips2">
            <span>{{ fileName }}</span>
          </div>
        </el-form-item>
        <el-form-item label="状态" prop="status">
          <el-radio-group v-model="ruleForm.status">
            <el-radio label="1">启用</el-radio>
            <el-radio label="0">禁用</el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="备注" prop="remark">
          <el-input style="width: 50%;margin-left: 10px" type="textarea" placeholder="请输入内容" v-model="ruleForm.remark" maxlength="30" show-word-limit></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="submitForm('ruleForm')">保存</el-button>
          <el-button @click="resetForm('ruleForm')">返回</el-button>
        </el-form-item>
      </el-form>
    </el-card>
  </div>
</template>

<script>
export default {
  data() {
    let that = this
    return {
      videoBaseUrl: this.$videoDownload,
      ruleForm: {
        id: '',
        title: '',
        status: '1',
        thumbnail: '',
        type: '1',
        webUrl: '',
        fileUrl: '',
        remark: ''
      },
      fileName: '',
      fileUrl: '',
      rules: {
        title: [{ required: true, message: '请输入标题', trigger: 'blur' }],
        thumbnail: [{ required: true, message: '请上传缩略图', trigger: 'blur' }]
      },
      type: '',
      loading: ''
    }
  },
  methods: {
    beforeExcelUpload(file) {
      const isLt2M = file.size / 1024 / 1024 < 100
      if (!isLt2M) {
        this.$message({
          message: '上传文件大小不能超过 100MB!',
          type: 'warning'
        })
      }
      this.loading = this.$loading({
          lock: true,
          text: '视频上传中，请耐心等待',
          spinner: 'el-icon-loading',
          background: 'rgba(0, 0, 0, 0.7)'
        });
      return isLt2M
    },
    uploadSuccess(res, file, fileList) {
      console.log(res, file, fileList)
      this.loading.close()
      this.fileName = res.data.fileName
      this.fileUrl = res.data.fileUrl
      this.ruleForm.fileUrl = res.data.fileUrl
    },
    uploadAction() {
      return this.$videoUpload
    },
    handleAvatarSuccess(res, file, fileList, index) {
      console.log(res, file, fileList, index)
      if (!res) return
      if (res.code == '0') {
        if (index === 0) {
          this.ruleForm.thumbnail = res.data.fileUrl
        }
      }
    },
    beforeAvatarUpload(file) {
      const isJPG = file.type === 'image/jpeg' || 'image/png'
      const isLt10M = file.size / 1024 / 1024 < 10

      if (!isJPG) {
        this.$message.error('上传图片只能是 JPG / png 格式!')
      }
      if (!isLt10M) {
        this.$message.error('上传图片大小不能超过10MB!')
      }
      return isJPG && isLt10M
    },

    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          if (this.ruleForm.type == 1 && !this.ruleForm.webUrl) {
            this.$message.error('请填写外部网站访问地址！')
            return
          } else if (this.ruleForm.type == 2 && !this.ruleForm.fileUrl) {
            this.$message.error('请上传本地视频文件！')
            return
          }
          if (this.ruleForm.type == 1) {
            this.ruleForm.fileUrl = ''
          } else {
            this.ruleForm.webUrl = ''
          }
          let params = Object.assign({}, this.ruleForm)
          if (this.$route.params.type === 'edit') {
            this.httpPost('/video/mod/' + params.id, params, true).then(res => {
              this.$message.success('修改成功')
              this.resetForm('ruleForm')
            })
          } else {
            this.httpPost('/video/add', params, true).then(res => {
              this.$message.success('增加成功')
              this.resetForm('ruleForm')
            })
          }
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },
    resetForm(formName) {
      this.$router.back()
    }
  },

  mounted() {
    const { params } = this.$route
    if (params && params.type === 'edit') {
      console.log(params.info)
      this.ruleForm = params.info
      this.ruleForm.status = this.ruleForm.status.toString()
      this.ruleForm.type = this.ruleForm.type.toString()
      this.fileName = this.ruleForm.fileUrl
      this.type = 2
    } else {
      this.type = 1
    }
  }
}
</script>

<style lang="scss" scoped>
/deep/.el-form-item {
  .el-input {
    width: 300px !important;
  }
  .el-textarea {
    width: 300px;
  }
}
.el-cascader {
  width: 300px;
}
.thumbnail {
  /deep/.el-upload--text {
    width: 178px;
    height: 178px;
    border: none;
  }
}
.local {
  /deep/.el-upload--text {
    border: 1px dashed #ddd;
  }
}
</style>
