<template>
  <div>
    <!-- 面包屑导航 -->
    <div class="breadcrumb">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item class="breadcrumb_item" :to="{ path: '/home' }">首页</el-breadcrumb-item>
        <el-breadcrumb-item>标准生活动漫视频</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <el-card>
      <el-form :inline="true" ref="queryForm" :model="queryForm" label-width="140px">
        <!-- <el-form-item label="关键字查询" prop="keyword">
          <el-input v-model="queryForm.keyword" placeholder="请输入搜索关键字"></el-input>
        </el-form-item> -->
        <el-form-item label="标题" prop="title">
          <el-input v-model="queryForm.title" placeholder="请输入标题"></el-input>
        </el-form-item>
        <el-form-item label="状态" prop="status">
          <el-select v-model="queryForm.status">
            <el-option label="全部" value=""></el-option>
            <el-option label="停用" value="0"></el-option>
            <el-option label="启用" value="1"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item class="select_btn">
          <el-button type="primary" @click="onSubmit">搜索</el-button>
          <el-button @click="resetForm('queryForm')">重置</el-button>
        </el-form-item>
      </el-form>

      <div class="MT_table_cont">
        <div class="btn">
          <el-button @click.stop="add" icon="el-icon-plus">新增</el-button>
          <el-button @click.stop="sort" icon="el-icon-plus">排序</el-button>
        </div>
        <!-- 表格列表 -->
        <el-table ref="multipleTable" :data="tableData" tooltip-effect="dark" style="width: 100%">
          <el-table-column prop="rownum_" label="序号" width="100"></el-table-column>
          <el-table-column prop="title" label="标题" width="200"></el-table-column>
          <el-table-column prop="thumbnail" label="缩略图" width="200">
            <template slot-scope="scope">
              <img class="thumbnail_img" width="80" @click="videoLink(scope.row)" :src="imgPath(scope.row.thumbnail)" alt="" />
            </template>
          </el-table-column>
          <el-table-column prop="type" label="来源" width="200">
            <template slot-scope="scope">
              {{ scope.row.type == 1 ? '外部网站' : '本地上传' }}
            </template>
          </el-table-column>
          <el-table-column label="视频地址">
            <template slot-scope="scope">
              {{ scope.row.type == 1 ? scope.row.webUrl : scope.row.fileUrl }}
            </template>
          </el-table-column>
          <el-table-column label="状态" width="100">
            <template slot-scope="scope">
              <el-tag v-if="scope.row.status == 1">启用</el-tag>
              <el-tag type="danger" v-else>禁用</el-tag>
            </template>
          </el-table-column>
          <!-- 操作 -->
          <el-table-column label="操作" width="150">
            <template slot-scope="scope">
              <el-link v-if="scope.row.status === 1" :underline="false" type="danger" @click.stop="close(scope.row.id)">禁用</el-link>
              <el-link v-else :underline="false" type="primary" @click.stop="open(scope.row.id)">启用</el-link>
              <el-link :underline="false" type="primary" @click.stop="edit(scope.row)">修改</el-link>
              <el-link :underline="false" type="danger" @click.stop="del(scope.row.id)">删除</el-link>
            </template>
          </el-table-column>
        </el-table>

        <!-- 分页器 -->
        <div class="MT_pagination" v-if="queryForm.total">
          <span class="jump_first" @click.stop="$jumpToFirst(queryForm, loadData)">首页</span>
          <span class="jump_last" @click.stop="$jumpToLast(queryForm, loadData)">尾页</span>
          <el-pagination
            background
            :total="queryForm.total"
            :current-page.sync="queryForm.page"
            :page-size.sync="queryForm.size"
            @current-change="loadData"
            layout="total, prev, pager, next, jumper"
          />
        </div>
      </div>
      <el-dialog :visible.sync="videoDialog" width="40%">
        <div class="demo">
          <video-player class="video-player vjs-custom-skin" ref="videoPlayer" :playsinline="true" :options="playerOptions"> </video-player>
        </div>
      </el-dialog>
    </el-card>
  </div>
</template>

<script>
export default {
  data() {
    let that = this
    return {
      queryForm: {
        status: '',
        title: '',
        size: 10,
        page: 1,
        total: ''
      },
      tableData: [],
      videoDialog: false,
      videoUrl: '',
      playerOptions: {
        //播放速度
        playbackRates: [0.5, 1.0, 1.5, 2.0],
        //如果true,浏览器准备好时开始回放。
        autoplay: false,
        // 默认情况下将会消除任何音频。
        muted: false,
        // 导致视频一结束就重新开始。
        loop: false,
        // 建议浏览器在<video>加载元素后是否应该开始下载视频数据。auto浏览器选择最佳行为,立即开始加载视频（如果浏览器支持）
        preload: 'auto',
        language: 'zh-CN',
        // 将播放器置于流畅模式，并在计算播放器的动态大小时使用该值。值应该代表一个比例 - 用冒号分隔的两个数字（例如"16:9"或"4:3"）
        aspectRatio: '16:9',
        // 当true时，Video.js player将拥有流体大小。换句话说，它将按比例缩放以适应其容器。
        fluid: true,
        sources: [
          {
            //类型
            type: 'video/webm', // 可以播放，用ogg也可打开
            type: 'video/ogg', // 可以播放，用webm也可打开
            type: 'video/3gp', // 可以播放
            type: 'video/mp4', // 可以播放
            //url地址
            src: ''
          }
        ],
        //你的封面地址
        poster: '',
        //允许覆盖Video.js无法播放媒体源时显示的默认信息。
        notSupportedMessage: '此视频暂无法播放，请稍后再试',
        controlBar: {
          timeDivider: true, // 当前时间和持续时间的分隔符
          durationDisplay: true, // 显示持续时间
          remainingTimeDisplay: false, // 是否显示剩余时间功能
          fullscreenToggle: true, // 是否显示全屏按钮
          volumeControl: true // 音量按键
        }
      }
    }
  },
  watch: {
    videoDialog(n, o) {
      if (n === false) {
        // console.log('1')
        // this.$refs.videoPlayer.player.play() // 播放
        this.$refs.videoPlayer.player.pause() // 暂停
        // this.$refs.videoPlayer.player.src(src) // 重置进度条
      }
    }
  },
  methods: {
    videoLink(info) {
      this.videoDialog = true
      if (info.type == 1) this.playerOptions.sources[0].src = info.webUrl
      else this.playerOptions.sources[0].src = this.$videoDownload + info.fileUrl
      this.playerOptions.poster = this.imgPath(info.thumbnail)
    },
    open(id) {
      this.$confirm('确定要启用该数据吗?', '提示', { type: 'warning' })
        .then(() => {
          this.httpPost('/video/enable/' + id, '').then(res => {
            this.$message.success('已成功启用')
            this.loadData()
          })
        })
        .catch(() => {})
    },

    close(id) {
      this.$confirm('确定要禁用该数据吗?', '提示', { type: 'warning' })
        .then(() => {
          this.httpPost('/video/disable/' + id, '').then(res => {
            this.$message.success('已成功禁用')
            this.loadData()
          })
        })
        .catch(() => {})
    },
    imgPath(thumbnail) {
      if (!thumbnail) return ''
      return this.$videoDownload + thumbnail
    },
    sort() {
      this.$router.push({ path: '/standardVideoSort', name: 'StandardVideoSort' })
    },
    // 新增
    add() {
      this.$router.push({ path: '/editStandardVideo', name: 'EditStandardVideo', params: { type: 'add' } })
    },

    // 修改
    edit(info) {
      this.$router.push({ path: '/editStandardVideo', name: 'EditStandardVideo', params: { type: 'edit', info } })
    },

    del(id) {
      this.$confirm('此操作将删除数据, 是否继续?', '提示', { type: 'warning' })
        .then(() => {
          this.httpPost('/video/rem/' + id, '').then(ret => {
            this.$message({ type: 'success', message: '删除成功!' })
            this.loadData()
          })
        })
        .catch(() => {})
    },

    onSubmit() {
      console.log(this.queryForm)
      this.loadData()
    },

    resetForm(formName) {
      this.$refs[formName].resetFields()
    },

    loadData() {
      this.queryForm.total = ''
      this.httpGet('/video/query', this.queryForm).then(res => {
        this.tableData = res.rows
        this.queryForm.total = res.total
      })
    }
  },

  mounted() {
    this.loadData()
  }
}
</script>

<style lang="scss" scoped>
/deep/.el-form {
  display: flex;
  flex-wrap: wrap;
  border: 1px solid #ddd;
  padding-top: 10px;
  margin-bottom: 20px;
  .select_btn .el-form-item__content {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    height: 40px;
    box-sizing: border-box;
    .choose_condition {
      padding: 0 10px;
      cursor: pointer;
    }
  }
}
/deep/.el-form {
  .el-input {
    width: 300px !important;
  }
}
.btn {
  margin-bottom: 10px;
}
.thumbnail_img {
  cursor: pointer;
}

.demo {
  display: inline-block;
  width: 100%;
  height: 400px;
  text-align: center;
  line-height: 100px;
  border: 1px solid transparent;
  border-radius: 4px;
  overflow: hidden;
  background: #fff;
  position: relative;
  box-shadow: 0 1px 1px rgba(0, 0, 0, 0.2);
  margin-right: 4px;
}

.demo:hover {
  display: block;
}
</style>
