<template>
  <div>
    <!-- 面包屑导航 -->
    <div class="breadcrumb">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item class="breadcrumb_item" :to="{ path: '/home' }">首页</el-breadcrumb-item>
        <el-breadcrumb-item :to="{ path: '/floatingPane' }">悬浮窗管理</el-breadcrumb-item>
        <el-breadcrumb-item>{{ $route.params.type === 'edit' ? '修改悬浮窗' : '新增悬浮窗' }}</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <el-card>
      <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="140px" class="demo-ruleForm">
        <el-form-item label="说明" prop="name">
          <el-input v-model="ruleForm.name" placeholder="请输入说明"></el-input>
        </el-form-item>
        <el-form-item label="缩略图" prop="fileUrl" class="fileUrl">
          <el-upload
            class="avatar-uploader-logo"
            :action="uploadAction()"
            :show-file-list="false"
            :on-success="(res, file, fileList) => handleAvatarSuccess(res, file, fileList, 0)"
            :before-upload="beforeAvatarUpload"
          >
            <img v-if="ruleForm.fileUrl" :src="videoBaseUrl + ruleForm.fileUrl" class="avatar" />
            <i v-else class="el-icon-plus avatar-uploader-icon"><i></i></i>
          </el-upload>
          <div class="font_size">
            <span>支持jpg／png格式 建议尺寸比例 宽 ：高 2 : 1</span>
            <!-- <span class="tips">小程序首页，头部切换门店及搜索栏背景图</span> -->
          </div>
        </el-form-item>
        <el-form-item label="链接地址" prop="linkHref">
          <el-input v-model="ruleForm.linkHref" placeholder="请输入链接地址"></el-input>
        </el-form-item>

        <el-form-item label="状态" prop="status">
          <el-radio-group v-model="ruleForm.status">
            <el-radio label="1">启用</el-radio>
            <el-radio label="0">禁用</el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="submitForm('ruleForm')">保存</el-button>
          <el-button @click="resetForm('ruleForm')">返回</el-button>
        </el-form-item>
      </el-form>
    </el-card>
  </div>
</template>

<script>
export default {
  data() {
    let that = this
    return {
      videoBaseUrl: this.$videoDownload,
      ruleForm: {
        id: '',
        name: '',
        status: '1',
        linkHref: '',
        fileUrl: ''
      },
      fileName: '',
      fileUrl: '',
      rules: {
        name: [{ required: true, message: '请输入标题', trigger: 'blur' }],
        fileUrl: [{ required: true, message: '请上传缩略图', trigger: 'blur' }],
        status: [{ required: true, message: '请选择状态', trigger: 'blur' }],
        linkHref: [{ required: true, message: '请输入链接地址', trigger: 'blur' }]
      },
      type: '',
      loading: ''
    }
  },
  methods: {
    beforeExcelUpload(file) {
      const isLt2M = file.size / 1024 / 1024 < 100
      if (!isLt2M) {
        this.$message({
          message: '上传文件大小不能超过 100MB!',
          type: 'warning'
        })
      }
      this.loading = this.$loading({
        lock: true,
        text: '视频上传中，请耐心等待',
        spinner: 'el-icon-loading',
        background: 'rgba(0, 0, 0, 0.7)'
      })
      return isLt2M
    },
    uploadAction() {
      return this.$floatUpload
    },
    handleAvatarSuccess(res, file, fileList, index) {
      console.log(res, file, fileList, index)
      if (!res) return
      if (res.code == '0') {
        if (index === 0) {
          this.ruleForm.fileUrl = res.data.fileUrl
        }
      }
    },
    beforeAvatarUpload(file) {
      const isJPG = file.type === 'image/jpeg' || 'image/png'
      const isLt10M = file.size / 1024 / 1024 < 10

      if (!isJPG) {
        this.$message.error('上传图片只能是 JPG / png 格式!')
      }
      if (!isLt10M) {
        this.$message.error('上传图片大小不能超过10MB!')
      }
      return isJPG && isLt10M
    },

    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          let params = Object.assign({}, this.ruleForm)
          if (this.$route.params.type === 'edit') {
            this.httpPost('/float/mod/' + params.id, params, true).then(res => {
              this.$message.success('修改成功')
              this.resetForm('ruleForm')
            })
          } else {
            this.httpPost('/float/add', params, true).then(res => {
              this.$message.success('增加成功')
              this.resetForm('ruleForm')
            })
          }
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },
    resetForm(formName) {
      this.$router.back()
      this.$refs[formName].resetFields()
    }
  },

  mounted() {
    const { params } = this.$route
    if (params && params.type === 'edit') {
      console.log(params.info)
      this.ruleForm = params.info
      this.ruleForm.status = this.ruleForm.status.toString()
      this.fileName = this.ruleForm.fileUrl
      this.type = 2
    } else {
      this.type = 1
    }
  }
}
</script>

<style lang="scss" scoped>
/deep/.el-form-item {
  .el-input {
    width: 300px !important;
  }
  .el-textarea {
    width: 300px;
  }
}
.el-cascader {
  width: 300px;
}
.fileUrl {
  /deep/.el-upload--text {
    width: 300px;
    height: 150px;
    border: none;
    .avatar-uploader-icon {
      width: 300px;
      height: 150px;
      line-height: 150px;
    }
  }
}
.avatar {
  width: 300px;
  height: 150px;
}
.local {
  /deep/.el-upload--text {
    border: 1px dashed #ddd;
  }
}
</style>
