<template>
  <div>
    <!-- 面包屑导航 -->
    <div class="breadcrumb">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item class="breadcrumb_item" :to="{ path: '/home' }">首页</el-breadcrumb-item>
        <el-breadcrumb-item :to="{ path: '/advertisement' }">广告宣传、风采展示</el-breadcrumb-item>
        <el-breadcrumb-item>{{ $route.params.type === 'audit' ? '审核' : '详情' }}</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <el-card>
      <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
        <el-row :gutter="20">
          <el-col :span="6">
            <div class="grid-content bg-purple">
              <el-form-item label="素材图片" prop="note">
                <img width="80" :src="imgPath(ruleForm.fileUrl)" alt="" />
              </el-form-item>
              <el-form-item label="联系人" prop="contactName">
                <span>{{ ruleForm.contactName }}</span>
              </el-form-item>
              <el-form-item label="联系电话" prop="contactPhone">
                <span>{{ ruleForm.contactPhone }}</span>
              </el-form-item>
              <el-form-item label="申请人" prop="userName">
                <span>{{ ruleForm.userName }}</span>
              </el-form-item>
              <el-form-item label="申请描述" prop="note">
                <span>{{ ruleForm.note }}</span>
              </el-form-item>
              <el-form-item label="申请时间" prop="note">
                <span>{{ ruleForm.createDate }}</span>
              </el-form-item>
            </div>
          </el-col>
          <el-col :span="6">
            <div class="grid-content bg-purple">
              <el-form-item label="是否启用" prop="playStatus">
                <el-tag v-if="ruleForm.playStatus == 1">启用</el-tag>
                <el-tag type="danger" v-else>禁用</el-tag>
              </el-form-item>
              <el-form-item label="链接地址" prop="linkHref">
                <span>{{ ruleForm.linkHref }}</span>
              </el-form-item>
              <el-form-item v-if="ruleForm.type !== 'audit'" label="审核人" prop="auditName">
                <span>{{ ruleForm.auditName }}</span>
              </el-form-item>
              <el-form-item v-if="ruleForm.type !== 'audit'" label="审核描述" prop="auditNote">
                <span>{{ ruleForm.auditNote }}</span>
              </el-form-item>
              <el-form-item v-if="ruleForm.type !== 'audit'" label="审核时间" prop="auditDate">
                <span>{{ ruleForm.auditDate }}</span>
              </el-form-item>
              <el-form-item v-if="ruleForm.type !== 'audit'" label="状态">
                <el-tag v-if="ruleForm.status == 0">申请中</el-tag>
                <el-tag type="cuccess" v-else-if="ruleForm.status == 1">已通过</el-tag>
                <el-tag type="danger" v-else>已拒绝</el-tag>
              </el-form-item>
              <el-form-item v-if="ruleForm.type === 'audit'" label="审核" prop="status">
                <el-radio-group v-model="ruleForm.status">
                  <el-radio label="1">通过</el-radio>
                  <el-radio label="2">拒绝</el-radio>
                </el-radio-group>
              </el-form-item>
              <el-form-item v-if="ruleForm.type === 'audit'" label="备注" prop="auditNote">
                <el-input style="width: 50%;margin-left: 10px" type="textarea" placeholder="请输入内容" v-model="ruleForm.auditNote" maxlength="30" show-word-limit></el-input>
              </el-form-item>
            </div>
          </el-col>
        </el-row>
        <el-form-item>
          <el-button v-if="ruleForm.type === 'audit'" type="primary" @click="submitForm('ruleForm')">保存</el-button>
          <el-button @click="resetForm('ruleForm')">返回</el-button>
        </el-form-item>
      </el-form>
    </el-card>
  </div>
</template>

<script>
export default {
  data() {
    let that = this
    return {
      videoBaseUrl: this.$videoDownload,
      ruleForm: {
        id: '',
        contactName: '',
        contactPhone: '',
        userName: '',
        note: '',
        createDate: '',
        playStatus: '',
        status: '',
        fileUrl: '',
        linkHref: '',
        auditName: '',
        auditNote: '',
        auditDate: ''
      },
      fileName: '',
      fileUrl: '',
      rules: {
        status: [{ required: true, message: '请选择审核结果', trigger: 'blur' }]
      }
    }
  },
  methods: {
    imgPath(thumbnail) {
      if (!thumbnail) return ''
      return this.$picDownload + thumbnail
    },

    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          console.log(this.ruleForm)
          if (this.ruleForm.status != 1 && this.ruleForm.status != 2) {
            return this.$message.warning('请选择审核结果'), false
          }
          let params = {
            auditNote: this.ruleForm.auditNote,
            status: this.ruleForm.status
          }
          this.httpPost('/advertisement/audit/' + this.ruleForm.id, params).then(res => {
            this.$message.success('审核成功')
            this.resetForm('ruleForm')
          })
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },
    resetForm(formName) {
      this.$router.back()
      this.$refs[formName].resetFields()
    }
  },

  mounted() {
    const { params } = this.$route
    let info = this.$sessionData('get', 'info')
    if (info.type === 'audit') {
      console.log(info)
      // info.status
      this.ruleForm = info
      this.ruleForm.status = this.ruleForm.status.toString()
    } else {
      this.ruleForm = info
    }
  }
}
</script>

<style lang="scss" scoped>
/deep/.el-form-item {
  .el-input {
    width: 300px !important;
  }
  .el-textarea {
    width: 300px !important;
  }
}
/deep/.el-form-item__content {
  line-height: 44px;
}
</style>
