<template>
  <div>
    <!-- 面包屑导航 -->
    <div class="breadcrumb">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item class="breadcrumb_item" :to="{ path: '/home' }">首页</el-breadcrumb-item>
        <el-breadcrumb-item :to="{ path: '/committee' }">委员会管理</el-breadcrumb-item>
        <el-breadcrumb-item>委员会详情</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <el-card>
      <el-form :model="ruleForm" ref="ruleForm" label-width="140px" class="demo-ruleForm">
        <span class="title">基础信息：</span>
        <el-row :gutter="20">
          <el-col :span="8">
            <div class="grid-content bg-purple">
              <el-form-item label="委员会全称：" prop="cnName">
                <span>{{ ruleForm.cnName }}</span>
              </el-form-item>
              <el-form-item label="委员会简称：" prop="shortName">
                <span>{{ ruleForm.shortName }}</span>
              </el-form-item>
              <el-form-item label="英文全称：" prop="enName">
                <span>{{ ruleForm.enName }}</span>
              </el-form-item>
            </div>
          </el-col>
          <el-col :span="8">
            <div class="grid-content bg-purple">
              <el-form-item label="本届届号：" prop="clazz">
                <span>{{ ruleForm.clazz }}</span>
              </el-form-item>
              <el-form-item label="委员会编号：" prop="code">
                <span>{{ ruleForm.code }}</span>
              </el-form-item>
              <el-form-item label="负责专业范围：" prop="range">
                <span>{{ ruleForm.range }}</span>
              </el-form-item>
            </div>
          </el-col>
          <el-col :span="8">
            <div class="grid-content bg-purple">
              <el-form-item label="筹建单位：" prop="prepareOrg">
                <span>{{ ruleForm.prepareOrg }}</span>
              </el-form-item>
              <el-form-item label="业务指导单位：" prop="guidanceOrg">
                <span>{{ ruleForm.guidanceOrg }}</span>
              </el-form-item>
            </div>
          </el-col>
        </el-row>

        <span class="title">秘书处信息：</span>
        <el-row :gutter="20">
          <el-col :span="8">
            <div class="grid-content bg-purple">
              <el-form-item label="现任秘书长：" prop="secretary">
                <span>{{ ruleForm.secretary }}</span>
              </el-form-item>
              <el-form-item label="联系人：" prop="contact">
                <span>{{ ruleForm.contact }}</span>
              </el-form-item>
              <el-form-item label="秘书处所在单位：" prop="company">
                <span>{{ ruleForm.company }}</span>
              </el-form-item>
            </div>
          </el-col>
          <el-col :span="8">
            <div class="grid-content bg-purple">
              <el-form-item label="所在省(市)：" prop="province">
                <span>{{ ruleForm.province }}</span>
              </el-form-item>
              <el-form-item label="通讯地址：" prop="address">
                <span>{{ ruleForm.address }}</span>
              </el-form-item>
              <el-form-item label="电话：" prop="phone">
                <span>{{ ruleForm.phone }}</span>
              </el-form-item>
            </div>
          </el-col>
          <el-col :span="8">
            <div class="grid-content bg-purple">
              <el-form-item label="传真：" prop="fax">
                <span>{{ ruleForm.fax }}</span>
              </el-form-item>
              <el-form-item label="邮编：" prop="zipCode">
                <span>{{ ruleForm.zipCode }}</span>
              </el-form-item>
              <el-form-item label="邮箱：" prop="email">
                <span>{{ ruleForm.email }}</span>
              </el-form-item>
            </div>
          </el-col>
        </el-row>

        |本届委员
        <span>{{ ruleForm.memberList }}</span>
      </el-form>
    </el-card>
  </div>
</template>

<script>
export default {
  data() {
    let that = this
    return {
      ruleForm: {
        code: '',
        id: '',
        cnName: '',
        range: '',
        enName: '',
        clazz: '',
        shortName: '',
        secretaryId: '',
        prepareOrg: '',
        guidanceOrg: '',
        secretary: '',
        contact: '',
        company: '',
        phone: '',
        province: '',
        email: '',
        address: '',
        fax: '',
        zipCode: '',
        memberList: ''
      },
      type: ''
    }
  },
  methods: {},

  mounted() {
    let id = this.$sessionData('get', 'committee_id')
    this.httpGet('/committee/detail/' + id, '').then(res => {
      this.ruleForm = res
    })
  }
}
</script>

<style lang="scss" scoped>
/deep/.el-form-item {
  .el-form-item__content {
    line-height: 44px !important;
  }
}
.info_item {
  width: 450px;
  margin-right: 100px;
}

.title {
  font-weight: bold;
  display: block;
  margin-bottom: 20px;
}
.secretiral,
.person,
.base_info {
  display: flex;
}
</style>
