<template>
  <div>
    <!-- 面包屑导航 -->
    <div class="breadcrumb">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item class="breadcrumb_item" :to="{ path: '/home' }">首页</el-breadcrumb-item>
        <el-breadcrumb-item :to="{ path: '/committee' }">技术委员会管理</el-breadcrumb-item>
        <el-breadcrumb-item>委员会设置标准</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <el-card>
      <el-form :inline="true" ref="queryForm" :model="queryForm" label-width="140px">
        <el-form-item label="标准类型" prop="type">
          <el-select v-model="queryForm.type" placeholder="请选择标准类型">
            <el-option label="全部" value=""></el-option>
            <el-option label="国家标准" value="1"></el-option>
            <el-option label="行业标准" value="2"></el-option>
            <el-option label="地方标准" value="3"></el-option>
            <el-option label="国际标准" value="4"></el-option>
            <el-option label="团体标准" value="4"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item class="select_btn">
          <el-button type="primary" @click="loadData">搜索</el-button>
        </el-form-item>
      </el-form>

      <div class="MT_table_cont">
        <!-- 按钮 -->
        <div class="btns">
          <el-button @click.stop="add" icon="el-icon-plus">新增</el-button>
          <div class="choose_btn _color">
            <span @click.stop="remove">批量删除</span>
          </div>
        </div>
        <!-- 表格列表 -->
        <el-table ref="multipleTable" :data="tableData" tooltip-effect="dark" style="width: 100%" @selection-change="handleSelectionChange">
          <el-table-column type="selection" width="55"> </el-table-column>
          <el-table-column prop="code" label="标准号" width="180">
            <template slot-scope="scope">
              <span class="choose_btn _color" onmouseover="this.style.fontWeight='bold'" onmouseout="this.style.fontWeight='normal'" style="cursor: pointer" @click="detail(scope.row.id)">
                {{ scope.row.code }}
              </span>
            </template>
          </el-table-column>
          <el-table-column prop="csName" label="标准名称"> </el-table-column>
          <el-table-column label="状态" width="120">
            <template slot-scope="scope">
              {{ scope.row.status === 'A' ? '现行' : '作废' }}
            </template>
          </el-table-column>
          <el-table-column label="标准文本" width="120">
            <template slot-scope="scope">
              {{ scope.row.ppbj === 0 ? '不包含' : '包含' }}
            </template>
          </el-table-column>
          <el-table-column prop="year" label="发布年代" width="120"> </el-table-column>
          <el-table-column prop="executeDate" label="实施日期" width="160"> </el-table-column>
          <!-- 操作 -->
          <el-table-column label="操作" width="80">
            <template slot-scope="scope">
              <el-link :underline="false" type="danger" @click.stop="del(scope.row.id)">删除</el-link>
            </template>
          </el-table-column>
        </el-table>

        <!-- 分页器 -->
        <div class="MT_pagination" v-if="queryForm.total">
          <span class="jump_first" @click.stop="$jumpToFirst(queryForm, loadData)">首页</span>
          <span class="jump_last" @click.stop="$jumpToLast(queryForm, loadData)">尾页</span>
          <el-pagination
            background
            :total="queryForm.total"
            :current-page.sync="queryForm.page"
            :page-size.sync="queryForm.size"
            @current-change="loadData"
            layout="total, prev, pager, next, jumper"
          />
        </div>
        <!-- 增加标准对话框 -->
        <el-dialog title="增加标准" :visible.sync="standardDialogFormVisible" width="40%">
          <el-divider></el-divider>
          <el-select v-model="addStandardId" filterable remote reserve-keyword placeholder="请输入关键词" :remote-method="searchStandard" :loading="loading">
            <el-option v-for="item in options" :key="item.id" :label="item.csName" :value="item.id"> </el-option>
          </el-select>
          <!-- <el-button @click="standardDialogFormVisible = false">取 消</el-button> -->
          <el-button type="primary" @click="addStandard">确 定</el-button>
        </el-dialog>
      </div>
    </el-card>
  </div>
</template>

<script>
export default {
  data() {
    let that = this
    return {
      queryForm: {
        // keyword: '',
        type: '',
        clazz: '',
        committeeId: '',
        size: 10,
        page: 1,
        total: ''
      },
      options: [],
      loading: false,
      addStandardId: '',
      standardDialogFormVisible: false,
      multipleSelection: [],
      tableData: []
    }
  },
  methods: {
    addStandard() {
      let standardIdList = []
      standardIdList.push(this.addStandardId)
      let params = {
        committeeId: this.queryForm.committeeId,
        clazz:this.queryForm.clazz,
        standardIdList: standardIdList
      }
      this.httpPost('/committee/standard/add', params).then(ret => {
        this.$message({ type: 'success', message: '增加成功!' })
        this.standardDialogFormVisible = false
        this.loadData()
      })
    },
    // 新增
    add() {
      this.standardDialogFormVisible = true
      this.options = []
    },
    searchStandard(query) {
      if (query !== '') {
        this.loading = true
        this.httpGet('/standard/query', { csName: query, page: 1, size: 10 }).then(res => {
          this.loading = false
          this.options = res.rows
        })
      } else {
        this.options = []
      }
    },

    //查看
    detail(id) {
      this.$router.push({ path: '/standardDetail', name: 'StandardDetail', params: { id } })
      this.$sessionData('set', 'stand_id', id)
    },

    del(id) {
      this.$confirm('此操作将删除数据, 是否继续?', '提示', { type: 'warning' })
        .then(() => {
          let standardIdList = []
          standardIdList.push(id)
          let params = {
            standardIdList: standardIdList,
            committeeId: this.queryForm.committeeId
          }
          this.httpPost('/committee/standard/rem', params).then(ret => {
            this.$message({ type: 'success', message: '删除成功!' })
            this.loadData()
          })
        })
        .catch(() => {})
    },

    // 批量删除
    remove() {
      if (!this.multipleSelection.length) return this.$message.warning('请选择要删除的数据！')
      this.$confirm('此操作将删除已选中数据, 是否继续?', '提示', { type: 'warning' })
        .then(() => {
          let standardIdList = []
          for (var i = 0; i < this.multipleSelection.length; i++) {
            standardIdList.push(this.multipleSelection[i].id)
          }
          let params = {
            standardIdList: standardIdList,
            committeeId: this.queryForm.committeeId
          }
          this.httpPost('/committee/standard/rem', params).then(ret => {
            this.$message({ type: 'success', message: '删除成功!' })
            this.loadData()
          })
        })
        .catch(() => {})
    },

    handleSelectionChange(val) {
      this.multipleSelection = val
    },

    loadData() {
      this.queryForm.total = ''
      this.httpGet('/committee/standard/query', this.queryForm).then(res => {
        this.tableData = res.rows
        this.queryForm.total = res.total
      })
    }
  },
  mounted() {

    let info = this.$sessionData('get', 'stand_info')
    this.queryForm.committeeId = info.id
    this.queryForm.clazz = info.clazz
    this.loadData()
  }
}
</script>

<style lang="scss" scoped>
/deep/.el-form {
  display: flex;
  flex-wrap: wrap;
  border: 1px solid #ddd;
  padding-top: 10px;
  margin-bottom: 20px;
  .select_btn .el-form-item__content {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    height: 40px;
    box-sizing: border-box;
    .choose_condition {
      padding: 0 10px;
      cursor: pointer;
    }
  }
}
.el-dialog .el-select {
  width: 400px;
  margin-right: 20px;
  padding-bottom: 50px;
}
</style>
