<template>
  <div>
    <!-- 面包屑导航 -->
    <div class="breadcrumb">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item class="breadcrumb_item" :to="{ path: '/home' }">首页</el-breadcrumb-item>
        <el-breadcrumb-item>悬浮窗管理</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <el-card>
      <el-form :inline="true" ref="queryForm" :model="queryForm" label-width="140px">
        <!-- <el-form-item label="关键字查询" prop="keyword">
          <el-input v-model="queryForm.keyword" placeholder="请输入搜索关键字"></el-input>
        </el-form-item> -->
        <el-form-item label="悬浮窗说明" prop="name">
          <el-input v-model="queryForm.name" placeholder="请输入悬浮窗说明"></el-input>
        </el-form-item>
        <el-form-item label="状态" prop="status">
          <el-select v-model="queryForm.status">
            <el-option label="全部" value=""></el-option>
            <el-option label="启用" value="1"></el-option>
            <el-option label="停用" value="0"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item class="select_btn">
          <el-button type="primary" @click="onSubmit">搜索</el-button>
          <el-button @click="resetForm('queryForm')">重置</el-button>
        </el-form-item>
      </el-form>

      <div class="MT_table_cont">
        <div class="btn">
          <el-button @click.stop="add" icon="el-icon-plus">新增</el-button>
        </div>
        <!-- 表格列表 -->
        <el-table ref="multipleTable" :data="tableData" tooltip-effect="dark" style="width: 100%">
          <el-table-column prop="thumbnail" label="缩略图" >
            <template slot-scope="scope">
              <img class="thumbnail_img" width="80" @click="goLink(scope.row.linkHref)" :src="imgPath(scope.row.fileUrl)" alt="" />
            </template>
          </el-table-column>
          <el-table-column prop="name" label="说明"></el-table-column>
          <el-table-column prop="linkHref" label="链接" width="200"></el-table-column>
          <el-table-column prop="createDate" label="创建时间" width="200"></el-table-column>
          <el-table-column label="状态" width="100">
            <template slot-scope="scope">
              <el-tag v-if="scope.row.status == 1">启用</el-tag>
              <el-tag type="danger" v-else>禁用</el-tag>
            </template>
          </el-table-column>
          <!-- 操作 -->
          <el-table-column label="操作" width="150">
            <template slot-scope="scope">
              <el-link v-if="scope.row.status == 1"  type="danger" @click.stop="close(scope.row.id)">禁用</el-link>
              <el-link v-else type="primary" @click.stop="open(scope.row.id)">启用</el-link>
              <el-link :underline="false" type="primary" @click.stop="edit(scope.row)">修改</el-link>
              <el-link :underline="false" type="danger" @click.stop="del(scope.row.id)">删除</el-link>
            </template>
          </el-table-column>
        </el-table>

        <!-- 分页器 -->
        <div class="MT_pagination" v-if="queryForm.total">
          <span class="jump_first" @click.stop="$jumpToFirst(queryForm, loadData)">首页</span>
          <span class="jump_last" @click.stop="$jumpToLast(queryForm, loadData)">尾页</span>
          <el-pagination
            background
            :total="queryForm.total"
            :current-page.sync="queryForm.page"
            :page-size.sync="queryForm.size"
            @current-change="loadData"
            layout="total, prev, pager, next, jumper"
          />
        </div>
      </div>
    </el-card>
  </div>
</template>

<script>
  export default {
    data() {
      let that = this
      return {
        queryForm: {
          status: '',
          name: '',
          size: 10,
          page: 1,
          total: ''
        },
        tableData: [],
      }
    },
    methods: {
      open(id) {
        this.$confirm('确定要启用该数据吗?', '提示', { type: 'warning' })
          .then(() => {
            this.httpPost('/float/enable/' + id, '').then(res => {
              this.$message.success('已成功启用')
              this.loadData()
            })
          })
          .catch(() => {})
      },

      close(id) {
        this.$confirm('确定要禁用该数据吗?', '提示', { type: 'warning' })
          .then(() => {
            this.httpPost('/float/disable/' + id, '').then(res => {
              this.$message.success('已成功禁用')
              this.loadData()
            })
          })
          .catch(() => {})
      },
      imgPath(thumbnail) {
        if (!thumbnail) return ''
        return this.$videoDownload + thumbnail
      },
      // 新增
      add() {
        this.$router.push({ path: '/editFloatingPane', name: 'EditFloatingPane', params: { type: 'add' } })
      },

      // 修改
      edit(info) {
        this.$router.push({ path: '/editFloatingPane', name: 'EditFloatingPane', params: { type: 'edit', info } })
      },

      del(id) {
        this.$confirm('此操作将删除数据, 是否继续?', '提示', { type: 'warning' })
          .then(() => {
            this.httpPost('/float/rem/' + id, '').then(ret => {
              this.$message({ type: 'success', message: '删除成功!' })
              this.loadData()
            })
          })
          .catch(() => {})
      },

      onSubmit() {
        console.log(this.queryForm)
        this.loadData()
      },

      resetForm(formName) {
        this.$refs[formName].resetFields()
      },
      goLink(url){
        window.open(url)
      },
      loadData() {
        this.queryForm.total = ''
        this.httpGet('/float/query', this.queryForm).then(res => {
          this.tableData = res.rows
          this.queryForm.total = res.total
        })
      }
    },

    mounted() {
      this.loadData()
    }
  }
</script>

<style lang="scss" scoped>
  /deep/.el-form {
    display: flex;
    flex-wrap: wrap;
    border: 1px solid #ddd;
    padding-top: 10px;
    margin-bottom: 20px;
    .select_btn .el-form-item__content {
      display: flex;
      align-items: center;
      justify-content: flex-start;
      height: 40px;
      box-sizing: border-box;
      .choose_condition {
        padding: 0 10px;
        cursor: pointer;
      }
    }
  }
  /deep/.el-form {
    .el-input {
      width: 300px !important;
    }
  }
  .btn {
    margin-bottom: 10px;
  }
  .thumbnail_img {
    cursor: pointer;
  }

  .demo {
    display: inline-block;
    width: 100%;
    height: 400px;
    text-align: center;
    line-height: 100px;
    border: 1px solid transparent;
    border-radius: 4px;
    overflow: hidden;
    background: #fff;
    position: relative;
    box-shadow: 0 1px 1px rgba(0, 0, 0, 0.2);
    margin-right: 4px;
  }

  .demo:hover {
    display: block;
  }
</style>
