<template>
  <div>
    <!-- 面包屑导航 -->
    <div class="breadcrumb">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item class="breadcrumb_item" :to="{ path: '/home' }">首页</el-breadcrumb-item>
        <el-breadcrumb-item>指标管理</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <el-card>
      <div class="main_cont" v-if="pageType === 0">
        <!-- 功能按钮 -->

        <div class="MT_fn_btn">
          <el-form :inline="true" :model="queryForm" class="btn_right demo-form-inline form_border">
            <!-- <div class="tips">默认显示30天内数据</div> -->
            <el-form-item label="" prop="status">
              <el-select v-model="queryForm.status" placeholder="请选择推送平台">
                <el-option label="指标名称" value="2"></el-option>
                <el-option label="标准编号或名称" value="1"></el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="" prop="keywords">
              <el-input v-model="queryForm.keywords" placeholder="请输入搜索内容"></el-input>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" @click.stop="onSubmit">搜索</el-button>
              <span style="margin-left: 20px"><i style="color: #409EFE">蓝色</i>表示该标准已经作废。</span>
            </el-form-item>
          </el-form>
        </div>

        <div class="MT_table_cont">
          <!-- 按钮 -->
          <div class="btns">
            <div class="btn_cont">
              <div class="choose_btn _color">
                <span @click.stop="exportData">批量删除</span>
              </div>
            </div>
          </div>
          <!-- 表格列表 -->
          <el-table ref="multipleTable" :data="tableData" tooltip-effect="dark" style="width: 100%" @selection-change="handleSelectionChange" :row-style="isRed">
            <el-table-column type="selection" width="55"> </el-table-column>
            <el-table-column prop="code" label="标准编号"> </el-table-column>
            <el-table-column prop="csName" label="标准名称"> </el-table-column>
            <el-table-column prop="indexName" label="指标名称"> </el-table-column>
            <el-table-column prop="indexValue" label="指标值"> </el-table-column>
            <!-- 操作 -->
            <el-table-column label="操作" width="100">
              <template slot-scope="scope">
                <el-link :underline="false" type="primary" @click.stop="edit(scope.row)">修改</el-link>
                <el-link :underline="false" type="danger" @click.stop="del(scope.row.id)">删除</el-link>
              </template>
            </el-table-column>
          </el-table>

          <!-- 分页器 -->
          <div class="MT_pagination" v-if="queryForm.total">
            <span class="jump_first" @click.stop="$jumpToFirst(queryForm, loadData)">首页</span>
            <span class="jump_last" @click.stop="$jumpToLast(queryForm, loadData)">尾页</span>
            <el-pagination
              background
              :total="queryForm.total"
              :current-page.sync="queryForm.page"
              :page-size.sync="queryForm.size"
              @current-change="loadData"
              layout="total, prev, pager, next, jumper"
            />
          </div>
        </div>
      </div>

      <el-form v-else :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
        <el-form-item label="指标名称" prop="indexName">
          <el-input size="small" style="width: 300px" v-model="ruleForm.indexName"></el-input>
        </el-form-item>
        <el-form-item label="指标值" prop="indexValue">
          <Tinymce style="width: 60%" ref="bzlc" :id="'tinymceBzlc'" @getCont="getCont"></Tinymce>
        </el-form-item>

        <el-form-item>
          <el-button type="primary" @click="saveForm('ruleForm')">保存</el-button>
          <el-button type="primary" @click="cancelForm">返回</el-button>
          <!-- <el-button @click="resetForm('ruleForm')">重置</el-button> -->
        </el-form-item>
      </el-form>
    </el-card>
  </div>
</template>

<script>
import Tinymce from '@/components/Tinymce/Tinymce'
export default {
  components: { Tinymce },
  data() {
    return {
      tableData: [],
      multipleSelection: [],
      queryForm: {
        status: '2',
        keywords: '',
        size: 10,
        page: 1,
        total: '',
        standardKeyword:'',
        indexName:''
      },
      pageType: 0,

      //==========
      ruleForm: {
        indexName: '',
        indexValue: ''
      },
      rules: {
        targetName: [
          { required: true, message: '请输入标题名称', trigger: 'blur' },
          { min: 3, max: 99, message: '长度在 3 到 30 个字符', trigger: 'blur' }
        ]
      }
    }
  },

  methods: {
    // 保存
    saveForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          this.httpPost("/standard/index/index/mod/"+this.ruleForm.id,this.ruleForm).then(res => {
            this.$message.success('修改成功')
            this.cancelForm(formName)
          })
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },

    // 富文本
    getCont(val) {
      this.ruleForm.indexValue = val
    },

    cancelForm() {
      this.pageType = 0
    },

    edit(info) {
      this.ruleForm = info
      this.pageType = 1
    },

    onSubmit() {
      this.loadData()
    },

    del(id) {
      this.$confirm('此操作将删除数据, 是否继续?', '提示', { type: 'warning' })
        .then(() => {
          let indexIdList =[]
          indexIdList.push(id)
          this.httpPost('/standard/index/index/rem', indexIdList).then(ret => {
            this.$message({ type: 'success', message: '删除成功!' })
            this.loadData()
          })
        })
        .catch(() => {})
    },
    // 批量删除
    remove() {
      if (!this.multipleSelection.length) return this.$message.warning('请选择要删除的数据！')
      this.$confirm('此操作将删除已选中数据, 是否继续?', '提示', { type: 'warning' })
        .then(() => {
          let indexIdList =[]
          for(var i = 0;i<this.multipleSelection.length;i++){
            indexIdList.push(this.multipleSelection[i].id)
          }
          this.httpPost('/standard/index/index/rem', indexIdList).then(ret => {
            this.$message({ type: 'success', message: '删除成功!' })
            this.loadData()
          })
        })
        .catch(() => {})
    },
    handleSelectionChange(val) {
      this.multipleSelection = val
    },

    loadData() {
      if(this.queryForm.status == '2'){
        this.queryForm.indexName = this.queryForm.keywords
        this.queryForm.standardKeyword = ''
      }else{
        this.queryForm.indexName = ''
        this.queryForm.standardKeyword = this.queryForm.keywords
      }
      this.queryForm.total = ''
      this.httpGet('/standard/index/index/query', this.queryForm).then(res => {
        this.tableData = res.rows
        this.queryForm.total = res.total
      })
    },

    isRed({ row, column, rowIndex, columnIndex }) {
      console.log(row, column, rowIndex, columnIndex)
      if (row.status === 'W') {
        return {
          color: '#409EFE'
        }
      }
    }
  },

  watch: {
    pageType(newVal, oldVal) {
      if (newVal !== 0) {
        this.$nextTick(() => {
          this.$refs.bzlc.editorValue = this.ruleForm.indexValue
        })
      }
    }
  },
  mounted() {
    this.loadData()
  }
}
</script>

<style lang="scss" scoped></style>
