<template>
  <div>
    <!-- 面包屑导航 -->
    <div class="breadcrumb">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item class="breadcrumb_item" :to="{ path: '/home' }">首页</el-breadcrumb-item>
        <el-breadcrumb-item :to="{ path: '/committee' }">委员会管理</el-breadcrumb-item>
        <el-breadcrumb-item>{{ $route.params.type === 'edit' ? '修改委员会' : $route.params.type === 'next' ? '开展下一届委员会' : '新增委员会' }}</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <el-card>
      <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="140px" class="demo-ruleForm">
        <span class="title">基础信息：</span>
        <div class="base_info">
          <div>
            <el-form-item label="委员会全称" prop="cnName">
              <el-input v-model="ruleForm.cnName" placeholder="请输入委员会全称"></el-input>
            </el-form-item>
            <el-form-item label="委员会简称" prop="shortName">
              <el-input v-model="ruleForm.shortName" placeholder="请输入委员会简称"></el-input>
            </el-form-item>
            <el-form-item label="英文全称" prop="enName">
              <el-input v-model="ruleForm.enName" placeholder="请输入英文全称"></el-input>
            </el-form-item>
          </div>
          <div>
            <el-form-item label="本届届号" prop="clazz">
              <el-input v-model="ruleForm.clazz" placeholder="请输入届号"></el-input>
            </el-form-item>
            <el-form-item label="委员会编号" prop="code">
              <el-input v-model="ruleForm.code" placeholder="请输入委员会编号"></el-input>
            </el-form-item>
            <el-form-item label="负责专业范围" prop="range">
              <el-input v-model="ruleForm.range" placeholder="请输入负责专业范围"></el-input>
            </el-form-item>
          </div>
          <div>
            <el-form-item label="筹建单位" prop="prepareOrg">
              <el-input v-model.number="ruleForm.prepareOrg" placeholder="请输入筹建单位"></el-input>
            </el-form-item>
            <el-form-item label="业务指导单位" prop="guidanceOrg">
              <el-input v-model="ruleForm.guidanceOrg" placeholder="请输入业务指导单位"></el-input>
            </el-form-item>
          </div>
        </div>

        <span class="title">秘书处信息：</span>
        <div class="secretiral">
          <div>
            <el-form-item label="现任秘书长" prop="secretary">
              <el-input v-model="ruleForm.secretary" placeholder="请输入现任秘书长"></el-input>
            </el-form-item>
            <el-form-item label="联系人" prop="contact">
              <el-input v-model="ruleForm.contact" placeholder="请输入联系人"></el-input>
            </el-form-item>
            <el-form-item label="秘书处所在单位" prop="company">
              <el-input v-model="ruleForm.company" placeholder="请输入秘书处所在单位"></el-input>
            </el-form-item>
          </div>
          <div>
            <el-form-item label="所在省(市)" prop="province">
              <el-input v-model="ruleForm.province" placeholder="请输入所在省（市）"></el-input>
            </el-form-item>
            <el-form-item label="通讯地址" prop="address">
              <el-input v-model="ruleForm.address" placeholder="请输入通讯地址"></el-input>
            </el-form-item>
            <el-form-item label="电话" prop="phone">
              <el-input v-model="ruleForm.phone" placeholder="请输入电话"></el-input>
            </el-form-item>
          </div>
          <div>
            <el-form-item label="传真" prop="fax">
              <el-input v-model="ruleForm.fax" placeholder="请输入传真"></el-input>
            </el-form-item>
            <el-form-item label="邮编" prop="zipCode">
              <el-input v-model="ruleForm.zipCode" placeholder="邮编"></el-input>
            </el-form-item>
            <el-form-item label="邮箱" prop="email">
              <el-input v-model="ruleForm.email" placeholder="请输入邮箱"></el-input>
            </el-form-item>
          </div>
        </div>

        <span class="title">本届委员：</span>
        <el-tag :key="tag" v-for="tag in dynamicTags" closable :disable-transitions="false" @close="handleClose(tag)">
          {{ tag }}
        </el-tag>
        <el-input class="input-new-tag" v-if="inputVisible" v-model="inputValue" ref="saveTagInput" size="small" @keyup.enter.native="handleInputConfirm" @blur="handleInputConfirm"> </el-input>
        <el-button v-else class="button-new-tag" size="small" @click="showInput">+ 新 增</el-button>

        <el-form-item class="fn_btn">
          <el-button type="primary" @click="submitForm('ruleForm')">保存</el-button>
          <el-button @click="resetForm('ruleForm')">返回</el-button>
        </el-form-item>
      </el-form>
    </el-card>
  </div>
</template>

<script>
export default {
  data() {
    let that = this
    return {
      ruleForm: {
        code: '',
        id: '',
        cnName: '',
        range: '',
        enName: '',
        clazz: '',
        shortName: '',
        secretaryId: '',
        prepareOrg: '',
        guidanceOrg: '',
        secretary: '',
        contact: '',
        company: '',
        phone: '',
        province: '',
        email: '',
        address: '',
        fax: '',
        zipCode: '',
        memberList: ''
      },
      dynamicTags: [],
      inputVisible: false,
      inputValue: '',
      rules: {
        code: [{ required: true, message: '请输入标准编号', trigger: 'blur' }],
        cnName: [{ required: true, message: '请输入委员会全称', trigger: 'blur' }],
        range: [{ required: true, message: '请输入负责专业范围', trigger: 'blur' }],
        enName: [{ required: true, message: '请输入英文全称', trigger: 'blur' }],
        clazz: [{ required: true, message: '请输入本届届号', trigger: 'blur' }],
        shortName: [{ required: true, message: '请输入委员会简称', trigger: 'blur' }],
        prepareOrg: [{ required: true, message: '请输入筹建单位', trigger: 'blur' }],
        guidanceOrg: [{ required: true, message: '请输入业务指导单位', trigger: 'blur' }],
        secretary: [{ required: true, message: '请输入秘书长', trigger: 'blur' }],
        contact: [{ required: true, message: '请输入联系人', trigger: 'blur' }],
        company: [{ required: true, message: '请输入秘书处所在单位', trigger: 'blur' }],
        phone: [{ required: true, message: '请输入电话', trigger: 'blur' }],
        province: [{ required: true, message: '请输入所在省（市）', trigger: 'blur' }],
        email: [{ required: true, message: '请输入邮箱', trigger: 'blur' }],
        address: [{ required: true, message: '请输入通讯地址', trigger: 'blur' }],
        fax: [{ required: true, message: '请输入传真', trigger: 'blur' }],
        zipCode: [{ required: true, message: '请输入邮编', trigger: 'blur' }]
      },
      type: ''
    }
  },
  methods: {
    handleClose(tag) {
      this.dynamicTags.splice(this.dynamicTags.indexOf(tag), 1)
    },

    showInput() {
      this.inputVisible = true
      this.$nextTick(_ => {
        this.$refs.saveTagInput.$refs.input.focus()
      })
    },

    handleInputConfirm() {
      let inputValue = this.inputValue
      if (inputValue) {
        this.dynamicTags.includes(inputValue) ? this.$message.warning('请勿重复添加') : this.dynamicTags.push(inputValue)
      }
      this.inputVisible = false
      this.inputValue = ''
    },
    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          this.ruleForm.memberList = JSON.stringify(this.dynamicTags)
          let params = Object.assign({}, this.ruleForm)
          if (this.$route.params.type === 'edit') {
            this.httpPost('/committee/mod/' + params.id, params).then(res => {
              this.$message.success('修改成功')
              this.resetForm('ruleForm')
            })
          } else if (this.$route.params.type === 'next') {
            this.httpPost('/committee/add/clazz/' + params.id, params).then(res => {
              this.$message.success('设置成功')
              this.resetForm('ruleForm')
            })
          } else {
            this.httpPost('/committee/add', params).then(res => {
              this.$message.success('增加成功')
              this.resetForm('ruleForm')
            })
          }
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },
    resetForm(formName) {
      this.$router.back()
      this.$refs[formName].resetFields()
    },
    detail() {
      const { params } = this.$route
      this.httpGet('/committee/detail/' + params.info.id, '').then(res => {
        this.ruleForm = res
        this.dynamicTags = eval(res.memberList)
        if (params.type === 'next') this.ruleForm.clazz = parseInt(this.ruleForm.clazz) + 1
      })
    }
  },

  mounted() {
    const { params } = this.$route
    if (params && params.type === 'edit') {
      this.detail()
      this.type = 2
    } else if (params && params.type === 'next') {
      this.detail()
      this.type = 3
    } else {
      this.type = 1
    }
  }
}
</script>

<style lang="scss" scoped>
.el-from {
}
/deep/.el-form-item {
  .el-input {
    width: 300px !important;
  }
  .el-textarea {
    width: 300px;
  }
}
.el-cascader {
  width: 300px;
}
.el-tag + .el-tag {
  margin-left: 10px;
}
.button-new-tag {
  margin-left: 10px;
  height: 32px;
  line-height: 30px;
  padding-top: 0;
  padding-bottom: 0;
}
.input-new-tag {
  width: 90px;
  margin-left: 10px;
  vertical-align: bottom;
}

.title {
  font-weight: bold;
  display: block;
  margin-bottom: 20px;
}
.secretiral,
.person,
.base_info {
  display: flex;
}
.el-tag {
  margin-left: 10px;
}
.el-tag,
.input-new-tag {
  margin-bottom: 10px;
}
.fn_btn /deep/.el-form-item__content {
  padding-top: 20px;
  margin-left: 0px !important;
}
</style>
