<template>
  <div>
    <!-- 面包屑导航 -->
    <div class="breadcrumb">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item class="breadcrumb_item" :to="{ path: '/home' }">首页</el-breadcrumb-item>
        <el-breadcrumb-item :to="{ path: '/standardVideo' }">标准生活动漫视频</el-breadcrumb-item>
        <el-breadcrumb-item>视频排序</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <el-card>
      <div class="banner" v-if="sortData.length">
        <el-row class="title">
          <el-col :span="6"><div class="grid-content bg-purple">视频图片预览</div></el-col>
          <el-col :span="6"><div class="grid-content bg-purple-light">视频标题</div></el-col>
          <el-col :span="6"><div class="grid-content bg-purple-light">视频来源</div></el-col>
          <el-col :span="6"><div class="grid-content bg-purple">操作</div></el-col>
        </el-row>
        <el-row class="banner_cont" v-for="(item, index) in sortData" :key="index">
          <el-col :span="6">
            <div class="grid-content bg-purple">
              <img style="width: 80px" :src="imgPath(item.thumbnail)" alt="" />
            </div>
          </el-col>
          <el-col :span="6">
            <div class="grid-content bg-purple-light">{{ item.title }}</div>
          </el-col>
          <el-col :span="6">
            <el-tag v-if="item.type == 1">外部网站</el-tag>
            <el-tag type="success" v-else>本地上传</el-tag>
          </el-col>
          <el-col :span="6">
            <div class="move_btn">
              <i :disabled="index === 0" @click="$toTop(index, sortData)"><img src="@/assets/images/top.png"/></i>
              <i :disabled="index === 0" @click="$toPrev(index, sortData)"><img src="@/assets/images/prev.png"/></i>
              <i :disabled="index === sortData.length - 1" @click="$toNext(index, sortData)"><img src="@/assets/images/next.png"/></i>
              <i :disabled="index === sortData.length - 1" @click="$toBottom(index, sortData)"><img src="@/assets/images/bottom.png"/></i>
            </div>
          </el-col>
        </el-row>
        <el-button type="primary" @click="submitForm">保存</el-button>
        <el-button @click="resetForm">返回</el-button>
      </div>
    </el-card>
  </div>
</template>

<script>
export default {
  data() {
    let that = this
    return {
      queryForm: {
        status: '1',
        size: 999,
        page: 1,
        total: ''
      },
      sortData: []
    }
  },
  methods: {
    resetForm() {
      this.$router.back()
    },
    submitForm() {
      let standardVideoSort = []
      for (var i = 0; i < this.sortData.length; i++) {
        standardVideoSort.push(this.sortData[i].id)
      }
      console.log(standardVideoSort)
      this.httpPost('/video/sort', standardVideoSort).then(res => {
        this.$router.back()
      })
    },
    imgPath(thumbnail) {
      if (!thumbnail) return ''
      return this.$picDownload + thumbnail
    },

    loadData() {
      this.queryForm.total = ''
      this.httpGet('/video/query', this.queryForm).then(res => {
        this.sortData = res.rows
      })
    }
  },

  mounted() {
    this.loadData()
  }
}
</script>

<style lang="scss" scoped>
.banner {
  margin: 50px;
}
.title {
  margin-bottom: 20px;
  padding-bottom: 10px;
  text-align: center;
  border-bottom: 1px solid #000;
  font-weight: bold;
}
.banner_cont {
  text-align: center;
  padding-bottom: 20px;
  border-bottom: 1px solid #ddd;
  margin-bottom: 20px;
}
.move_btn {
  line-height: 16px;
  i {
    display: inline-block;
    padding: 2px;
    border: 1px solid #333;
    border-radius: 3px;
    margin: 0 10px;
    cursor: pointer;
  }
}
.el-button {
  margin-top: 20px;
}
</style>
