<template>
  <div>
    <!-- 面包屑导航 -->
    <div class="breadcrumb">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item class="breadcrumb_item" :to="{ path: '/home' }">首页</el-breadcrumb-item>
        <el-breadcrumb-item>技术委员会管理</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <el-card>
      <el-form :inline="true" ref="queryForm" :model="queryForm">
        <!-- <el-form-item label="关键字查询" prop="keyword">
          <el-input v-model="queryForm.keyword" placeholder="请输入搜索关键字"></el-input>
        </el-form-item> -->
        <el-form-item label="委员会简称" prop="shortName">
          <el-input v-model="queryForm.shortName" placeholder="请输入委员会简称"></el-input>
        </el-form-item>
        <el-form-item label="专业范围" prop="range">
          <el-input v-model="queryForm.range" placeholder="请输入专业范围"></el-input>
        </el-form-item>
        <el-form-item label="编号" prop="code">
          <el-input v-model="queryForm.code" placeholder="请输入编号"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="onSubmit">搜索</el-button>
          <el-button @click="resetForm('queryForm')">重置</el-button>
        </el-form-item>
      </el-form>

      <div class="MT_table_cont">
        <div class="btns">
          <el-button @click.stop="add" icon="el-icon-plus">新增</el-button>
        </div>
        <!-- 表格列表 -->
        <el-table ref="multipleTable" :data="tableData" tooltip-effect="dark" style="width: 100%">
          <!-- <el-table-column type="selection" width="55"> </el-table-column> -->
          <el-table-column prop="rownum_" label="序号" width="100"></el-table-column>
          <el-table-column prop="code" label="委员会编号" width="100">
            <template slot-scope="scope">
              <span class="choose_btn _color" onmouseover="this.style.fontWeight='bold'" onmouseout="this.style.fontWeight='normal'" @click="detail(scope.row.id)">{{ scope.row.code }}</span>
            </template>
          </el-table-column>
          <el-table-column prop="shortName" label="委员会名称"> </el-table-column>
          <el-table-column prop="company" label="秘书处所在单位"> </el-table-column>
          <el-table-column prop="range" label="负责专业范围" show-overflow-tooltip> </el-table-column>
          <el-table-column prop="clazz" label="现为第几届" width="130"> </el-table-column>
          <!-- 操作 -->
          <el-table-column label="操作" width="220">
            <template slot-scope="scope">
              <el-link :underline="false" type="primary" @click.stop="edit(scope.row)">修改</el-link>
              <el-link :underline="false" type="primary" @click.stop="setStandard(scope.row)">标准设置</el-link>
              <el-link :underline="false" type="primary" @click.stop="nextClazz(scope.row)">生成下届</el-link>
              <el-link :underline="false" type="danger" @click.stop="del(scope.row.id)">删除</el-link>
            </template>
          </el-table-column>
        </el-table>

        <!-- 分页器 -->
        <div class="MT_pagination" v-if="queryForm.total">
          <span class="jump_first" @click.stop="$jumpToFirst(queryForm, loadData)">首页</span>
          <span class="jump_last" @click.stop="$jumpToLast(queryForm, loadData)">尾页</span>
          <el-pagination
            background
            :total="queryForm.total"
            :current-page.sync="queryForm.page"
            :page-size.sync="queryForm.size"
            @current-change="loadData"
            layout="total, prev, pager, next, jumper"
          />
        </div>
      </div>
    </el-card>
  </div>
</template>

<script>
export default {
  data() {
    let that = this
    return {
      queryForm: {
        // keyword: '',
        code: '',
        range: '',
        shortName: '',
        size: 10,
        page: 1,
        total: ''
      },
      tableData: [],
      editInfo: {}
    }
  },
  methods: {
    // 新增
    add() {
      this.$router.push({ path: '/editCommittee', name: 'EditCommittee', params: { type: 'add' } })
    },

    // 修改
    edit(info) {
      this.$router.push({ path: '/editCommittee', name: 'EditCommittee', params: { type: 'edit', info } })
    },
    //下一届
    nextClazz(info) {
      this.$router.push({ path: '/editCommittee', name: 'EditCommittee', params: { type: 'next', info } })
    },
    // 设置标准
    setStandard(info) {
      this.$router.push({ path: '/committeeStandard', name: 'CommitteeStandard', params: { info } })
      this.$sessionData('set', 'stand_info', info)
    },

    //查看
    detail(id) {
      this.$router.push({ path: '/committeeDetail', name: 'CommitteeDetail', params: { id } })
      this.$sessionData('set', 'committee_id', id)
    },

    del(id) {
      this.$confirm('此操作将删除数据, 是否继续?', '提示', { type: 'warning' })
        .then(() => {
          this.httpPost('/standard/rem/' + id, '').then(ret => {
            this.$message({ type: 'success', message: '删除成功!' })
            this.loadData()
          })
        })
        .catch(() => {})
    },

    onSubmit() {
      console.log(this.queryForm)
      this.loadData()
    },

    resetForm(formName) {
      this.$refs[formName].resetFields()
    },

    loadData() {
      this.queryForm.total = ''
      this.httpGet('/committee/query', this.queryForm).then(res => {
        this.tableData = res.rows
        this.queryForm.total = res.total
      })
    }
  },

  mounted() {
    this.loadData()
  }
}
</script>

<style lang="scss" scoped>
/deep/.el-form {
  display: flex;
  flex-wrap: wrap;
  border: 1px solid #ddd;
  padding: 10px 10px 0;
  margin-bottom: 20px;
  .select_btn .el-form-item__content {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    height: 40px;
    box-sizing: border-box;
    .choose_condition {
      padding: 0 10px;
      cursor: pointer;
    }
  }
  .el-form-item {
    .el-input {
      width: 200px !important;
    }
  }
}
/deep/.el-form {
  .el-input {
    width: 300px !important;
  }
}
.choose_btn {
  cursor: pointer;
  text-decoration: underline;
}
</style>
