<template>
  <div>
    <!-- 面包屑导航 -->
    <div class="breadcrumb">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item class="breadcrumb_item" :to="{ path: '/home' }">首页</el-breadcrumb-item>
        <el-breadcrumb-item>广告宣传、风采展示</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <el-card>
      <el-form ref="queryForm" :model="queryForm" label-width="140px">
        <el-form-item label="联系人" prop="contactName">
          <el-input v-model="queryForm.contactName" placeholder="请输入联系人"></el-input>
        </el-form-item>
        <el-form-item label="联系电话" prop="contactPhone">
          <el-input v-model="queryForm.contactPhone" placeholder="请输入联系电话"></el-input>
        </el-form-item>
        <el-form-item label="状态" prop="status">
          <el-select v-model="queryForm.status">
            <el-option label="全部" value=""></el-option>
            <el-option label="申请" value="0"></el-option>
            <el-option label="通过" value="1"></el-option>
            <el-option label="拒绝" value="2"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="是否启用" prop="status">
          <el-select v-model="queryForm.playStatus">
            <el-option label="全部" value=""></el-option>
            <el-option label="停用" value="0"></el-option>
            <el-option label="启用" value="1"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="申请时间" prop="chooseDate">
          <el-date-picker size="small" @change="changeDate" v-model="queryForm.chooseDate" type="daterange" range-separator="至" start-placeholder="开始日期" end-placeholder="结束日期">
          </el-date-picker>
        </el-form-item>
        <el-form-item class="select_btn">
          <el-button type="primary" @click="onSubmit">搜索</el-button>
          <el-button @click="resetForm('queryForm')">重置</el-button>
        </el-form-item>
      </el-form>

      <div class="MT_table_cont">
        <!-- 表格列表 -->
        <el-table ref="multipleTable" :data="tableData" tooltip-effect="dark" style="width: 100%">
          <el-table-column prop="fileUrl" label="素材图片" width="100">
            <template slot-scope="scope">
              <img width="80" :src="imgPath(scope.row.fileUrl)" alt="" />
            </template>
          </el-table-column>
          <el-table-column prop="contactName" label="联系人" width="100"></el-table-column>
          <el-table-column prop="contactPhone" label="联系电话" width="120"></el-table-column>
          <el-table-column prop="userName" label="申请人" width="100"></el-table-column>
          <el-table-column prop="note" label="申请描述" show-overflow-tooltip></el-table-column>
          <el-table-column prop="createDate" label="申请时间" width="180"></el-table-column>
          <!--<el-table-column prop="linkHref" label="链接地址"></el-table-column>-->
          <!--<el-table-column prop="auditName" label="审核人" width="100"></el-table-column>-->
          <!--<el-table-column prop="auditNote" label="审核描述" width="200"></el-table-column>-->
          <!--<el-table-column prop="auditDate" label="审核时间" width="150"></el-table-column>-->
          <el-table-column prop="status" label="状态" width="100">
            <template slot-scope="scope">
              <el-tag v-if="scope.row.status == 0">申请中</el-tag>
              <el-tag type="success" v-else-if="scope.row.status == 1">已通过</el-tag>
              <el-tag type="danger" v-else>已拒绝</el-tag>
            </template>
          </el-table-column>

          <el-table-column prop="playStatus" label="是否启用" width="100">
            <template slot-scope="scope">
              <el-tag v-if="scope.row.playStatus == 1">启用</el-tag>
              <el-tag type="danger" v-else>禁用</el-tag>
            </template>
          </el-table-column>
          <!-- 操作 -->
          <el-table-column label="操作" width="180">
            <template slot-scope="scope">
              <el-link :underline="false" type="primary" @click.stop="detail(scope.row)">查看</el-link>
              <el-link v-if="scope.row.playStatus === 1" :underline="false" type="danger" @click.stop="close(scope.row.id)">禁用</el-link>
              <el-link v-else :underline="false" type="primary" @click.stop="open(scope.row.id)">启用</el-link>
              <el-link v-if="scope.row.status === 0" :underline="false" type="primary" @click.stop="audit(scope.row)">审核</el-link>
              <el-link :underline="false" type="danger" @click.stop="del(scope.row.id)">删除</el-link>
            </template>
          </el-table-column>
        </el-table>

        <!-- 分页器 -->
        <div class="MT_pagination" v-if="queryForm.total">
          <span class="jump_first" @click.stop="$jumpToFirst(queryForm, loadData)">首页</span>
          <span class="jump_last" @click.stop="$jumpToLast(queryForm, loadData)">尾页</span>
          <el-pagination
            background
            :total="queryForm.total"
            :current-page.sync="queryForm.page"
            :page-size.sync="queryForm.size"
            @current-change="loadData"
            layout="total, prev, pager, next, jumper"
          />
        </div>
      </div>
    </el-card>
  </div>
</template>

<script>
export default {
  data() {
    let that = this
    return {
      queryForm: {
        status: '',
        playStatus: '',
        contactName: '',
        contactPhone: '',
        startDate: '',
        endDate: '',
        chooseDate: '',
        size: 10,
        page: 1,
        total: ''
      },
      tableData: []
    }
  },
  methods: {
    changeDate(e) {
      if (!e) {
        this.queryForm.startDate = ''
        this.queryForm.endDate = ''
      } else {
        this.queryForm.startDate = this.$moment(e[0]).format('YYYYMMDD')
        this.queryForm.endDate = this.$moment(e[1]).format('YYYYMMDD')
      }
      console.log(this.queryForm)
    },
    open(id) {
      this.$confirm('确定要启用该素材吗?', '提示', { type: 'warning' })
        .then(() => {
          this.httpPost('/advertisement/enable/' + id, '').then(res => {
            this.$message.success('已成功启用')
            this.loadData()
          })
        })
        .catch(() => {})
    },

    close(id) {
      this.$confirm('确定要禁用该素材吗?', '提示', { type: 'warning' })
        .then(() => {
          this.httpPost('/advertisement/disable/' + id, '').then(res => {
            this.$message.success('已成功禁用')
            this.loadData()
          })
        })
        .catch(() => {})
    },
    imgPath(thumbnail) {
      if (!thumbnail) return ''
      return this.$adDownload + thumbnail
    },

    // 审核
    audit(info) {
      info.type = 'audit'
      this.$router.push({ path: '/auditAdvertisement', name: 'AuditAdvertisement', params: { type: 'audit', info } })
      this.$sessionData('set', 'info', info)
    },
    // 查看
    detail(info) {
      info.type = 'detail'
      this.$router.push({ path: '/auditAdvertisement', name: 'AuditAdvertisement', params: { type: 'detail', info } })
      this.$sessionData('set', 'info', info)
    },

    del(id) {
      this.$confirm('此操作将删除数据, 是否继续?', '提示', { type: 'warning' })
        .then(() => {
          this.httpPost('/advertisement/rem/' + id, '').then(ret => {
            this.$message({ type: 'success', message: '删除成功!' })
            this.loadData()
          })
        })
        .catch(() => {})
    },

    onSubmit() {
      console.log(this.queryForm)
      this.loadData()
    },

    resetForm(formName) {
      this.$refs[formName].resetFields()
    },

    loadData() {
      this.queryForm.total = ''
      this.httpGet('/advertisement/query', this.queryForm).then(res => {
        this.tableData = res.rows
        this.queryForm.total = res.total
      })
    }
  },

  mounted() {
    this.loadData()
  }
}
</script>

<style lang="scss" scoped>
/deep/.el-form {
  display: flex;
  flex-wrap: wrap;
  border: 1px solid #ddd;
  padding-top: 10px;
  margin-bottom: 20px;
  .select_btn .el-form-item__content {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    height: 40px;
    box-sizing: border-box;
    .choose_condition {
      padding: 0 10px;
      cursor: pointer;
    }
  }
}
/deep/.el-form {
  .el-input {
    width: 300px !important;
  }
  .el-date-editor {
    width: 300px;
  }
}
</style>
